import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Constants } from '../../constants';
import { Cookie } from 'ng2-cookies';
import { saveAs } from '@progress/kendo-file-saver';
declare var $: any;

@Injectable({
  providedIn: 'root'
})
export class ReportService {

  constructor(private httpClient: HttpClient) {
  }

  getReportTypes() {
    return this.httpClient.get("assets/reportTypes.json")
  }

  getAreaDetails() {
    return this.httpClient.get(Constants.HOME_URL + 'getAllArea');
  }

  getTableData(reportTypeId, areaId) {
    if (reportTypeId == 2)
      return this.httpClient.get(Constants.HOME_URL + "getDailyA03?forAreaId=" + areaId)
    if (reportTypeId == 1)
      return this.httpClient.get(Constants.HOME_URL + "weeklyExcel?parentAreaId=" + areaId)

  }

  downloadPdf(reportTypeId, areaId) {
    if (reportTypeId == 2) {
      // $('<form action="'+ Constants.HOME_URL + "downloadDailyA03PdfReport?forAreaId=" + areaId +'" method="GET"></form>')
      // .appendTo('body').submit().remove();
      this.httpClient.get(Constants.HOME_URL + "downloadDailyA03PdfReport?forAreaId=" + areaId).subscribe(res => {
        console.log(res);
        this.download('fileName='+res+'&access_token=' +Cookie.get("access_token"));   
      }, error => {
        console.log(error);
        // var byteString = atob(<string>error.error.text);

        // // Convert that text into a byte array.
        // var ab = new ArrayBuffer(byteString.length);
        // var ia = new Uint8Array(ab);
        // for (var i = 0; i < byteString.length; i++) {
        //   ia[i] = byteString.charCodeAt(i);
        // }

        // // Blob for saving.
        // var blob = new Blob([ia], { type: "application/pdf" });

        // // Tell the browser to save as report.pdf.
        // saveAs(blob, "report.pdf");
      });
      // this.download(Constants.HOME_URL + "downloadDailyA03PdfReport", 'forAreaId='+ areaId +'&access_token=' +Cookie.get("access_token"))
    }
    if (reportTypeId == 1) {
      this.httpClient.get(Constants.HOME_URL + "weeklyPdf?parentAreaId=" + areaId).subscribe(res => {
        console.log(res)
        this.download('fileName='+res+'&access_token=' +Cookie.get("access_token"));  
      });
    }
  }

  download(data) {
    if (data) {
      //data can be string of parameters or array/object
      data = typeof data == 'string' ? data : $.param(data);
      //split params into form inputs
      var inputs = '';
      let url = Constants.HOME_URL + 'downloadReport';
      $.each(data.split('&'), function () {
        var pair = this.split('=');
        inputs += '<input type="hidden" name="' + pair[0] + '" value="' + pair[1] + '" />';
      });
      //send request
      $('<form action="' + url + '" method="post">' + inputs + '</form>')
        .appendTo('body').submit().remove();
    };

  }
}
